<template>
  <view class="main-container">
    <view class="header">
      <text class="page-title">Â≠¶‰π†Âä©Êâã</text>
      <view class="user-info" @click="logout">
        <view class="avatar">
          <text class="avatar-text">{{ userInfo ? userInfo.username?.[0]?.toUpperCase() || 'U' : 'U' }}</text>
        </view>
        <view class="user-details">
          <text class="user-name">{{ userInfo ? userInfo.username || 'Ê∏∏ÂÆ¢' : 'Ê∏∏ÂÆ¢' }}</text>
          <text class="logout-text">ÁÇπÂáªÈÄÄÂá∫</text>
        </view>
      </view>
    </view>
    
    <view class="stats-container">
      <view class="stat-card">
        <text class="stat-number">{{ statistics.taskCount }}</text>
        <text class="stat-label">ÂæÖÂäû‰ªªÂä°</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ statistics.noteCount }}</text>
        <text class="stat-label">Á¥ØËÆ°Á¨îËÆ∞</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ statistics.learningStreak }}</text>
        <text class="stat-label">ËøûÁª≠Â≠¶‰π†</text>
      </view>
    </view>
    
    <!-- Âä†ËΩΩÊåáÁ§∫Âô® -->
    <view v-if="loading" class="loading-container">
      <text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
    </view>
    
    <view class="today-tasks">
      <view class="section-header">
        <text class="section-title">‰ªäÊó•ÂæÖÂäû</text>
        <text class="section-more" @click="viewAllTasks('today')">Êü•ÁúãÂÖ®ÈÉ®</text>
      </view>
      <view class="task-list">
        <view v-if="todayTaskList.length > 0">
          <view class="task-item" v-for="(task, index) in todayTaskList" :key="index">
            <checkbox class="task-checkbox" :checked="false" @change="handleTaskComplete(task)"></checkbox>
            <view class="task-content">
              <text class="task-title">{{ task.title }}</text>
              <text class="task-time">ËøòÊúâ{{ task.time }}Êà™Ê≠¢</text>
            </view>
          </view>
        </view>
        <view v-else class="empty-state">
          <view class="empty-icon">üìù</view>
          <text class="empty-text">ÊöÇÊó†‰ªäÊó•ÂæÖÂäû‰ªªÂä°</text>
          <text class="empty-tip">ÁÇπÂáªÊü•ÁúãÂÖ®ÈÉ®ÂàõÂª∫Êñ∞‰ªªÂä°</text>
        </view>
      </view>
    </view>
    
    <view class="upcoming-tasks">
      <view class="section-header">
        <text class="section-title">Âç≥Â∞ÜÊà™Ê≠¢(3Â§©ÂÜÖ)</text>
        <text class="section-more" @click="viewAllTasks('upcoming')">Êü•ÁúãÂÖ®ÈÉ®</text>
      </view>
      <view class="task-list">
        <view v-if="upcomingTaskList.length > 0">
          <view class="task-item" v-for="(task, index) in upcomingTaskList" :key="index">
            <checkbox class="task-checkbox" :checked="false" @change="handleTaskComplete(task)"></checkbox>
            <view class="task-content">
              <text class="task-title">{{ task.title }}</text>
              <view class="task-deadline">
                <text class="deadline-label">{{ task.deadline }}</text>
                <view class="priority-tag" :class="task.priority">{{ task.priorityText }}</view>
              </view>
            </view>
          </view>
        </view>
        <view v-else class="empty-state">
          <view class="empty-icon">‚è∞</view>
          <text class="empty-text">ÊöÇÊó†Âç≥Â∞ÜÊà™Ê≠¢‰ªªÂä°</text>
          <text class="empty-tip">3Â§©ÂÜÖÁöÑ‰ªªÂä°Â∞ÜÊòæÁ§∫Âú®ËøôÈáå</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
// ÂØºÂÖ•Áî®Êà∑API„ÄÅ‰ªªÂä°APIÂíåÂ≠òÂÇ®Â∑•ÂÖ∑
import { userAPI } from '../../api';
import { getTodayTasks, getUpcomingTasks } from '../../api/task';
import { getUserInfo, clearUserData } from '../../utils/storage';

export default {
  data() {
    return {
      userInfo: null,
      todayTaskList: [],
      upcomingTaskList: [],
      loading: false,
      statistics: {
        taskCount: 0,
        noteCount: 12,
        learningStreak: 7
      }
    }
  },
  onLoad() {
    // È°µÈù¢Âä†ËΩΩÊó∂Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂíå‰ªªÂä°Êï∞ÊçÆ
    console.log('Âä†ËΩΩÂÆåÊàê')
    this.loadUserInfo();
    this.fetchTasks();
  },
  methods: {
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    loadUserInfo() {
      this.userInfo = getUserInfo();
      console.log('userInfo', this.userInfo)
      if (!this.userInfo) {
        // Â¶ÇÊûúÊú¨Âú∞Ê≤°ÊúâÁî®Êà∑‰ø°ÊÅØÔºåÂ∞ùËØï‰ªéÊúçÂä°Âô®Ëé∑Âèñ
        this.fetchUserInfo();
      } else {
        // Â¶ÇÊûúÊúâÁî®Êà∑‰ø°ÊÅØÔºåÊõ¥Êñ∞‰ªªÂä°ÁªüËÆ°‰∏≠ÁöÑ‰ªªÂä°Êï∞Èáè
        this.statistics.taskCount = this.todayTaskList.length + this.upcomingTaskList.length;
      }
    },
    
    // Ëé∑Âèñ‰ªªÂä°Êï∞ÊçÆ
    async fetchTasks() {
      if (!this.userInfo) return;
      
      this.loading = true;
      try {
        // Âπ∂Ë°åËé∑Âèñ‰ªäÊó•ÂæÖÂäûÂíåÂç≥Â∞ÜÊà™Ê≠¢‰ªªÂä°
        const [todayRes, upcomingRes] = await Promise.all([
          getTodayTasks({ userId: this.userInfo.uid }),
          getUpcomingTasks({ userId: this.userInfo.uid })
        ]);
        
        // Â§ÑÁêÜ‰ªäÊó•ÂæÖÂäû‰ªªÂä°Êï∞ÊçÆ
        if (todayRes.code === 200 && todayRes.data) {
          this.todayTaskList = (todayRes.data.records || []).map(task => ({
            id: task.taskId,
            title: task.taskName || 'Êú™ÂëΩÂêç‰ªªÂä°',
            time: this.formatTaskTime(task)
          }));
        }
        
        // Â§ÑÁêÜÂç≥Â∞ÜÊà™Ê≠¢‰ªªÂä°Êï∞ÊçÆ
        if (upcomingRes.code === 200 && upcomingRes.data) {
          this.upcomingTaskList = (upcomingRes.data.records || []).map(task => ({
            id: task.taskId,
            title: task.taskName || 'Êú™ÂëΩÂêç‰ªªÂä°',
            deadline: this.formatDeadline(task.deadline),
            priority: this.formatPriority(task.priority),
            priorityText: this.formatPriorityText(task.priority)
          }));
        }
        
        // Êõ¥Êñ∞‰ªªÂä°ÁªüËÆ°
        this.statistics.taskCount = this.todayTaskList.length;
      } catch (error) {
        console.error('Ëé∑Âèñ‰ªªÂä°Êï∞ÊçÆÂ§±Ë¥•:', error);
        // Â§±Ë¥•Êó∂‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ‰Ωú‰∏∫ÂêéÂ§á
        this.useMockData();
      } finally {
        this.loading = false;
      }
    },
    
    // Ê†ºÂºèÂåñ‰ªªÂä°Êó∂Èó¥ÊòæÁ§∫
    formatTaskTime(task) {
      if (!task.subjectName) return '';
      
      // Â§ÑÁêÜ‰ªªÂä°Êó∂Èó¥
      const today = new Date();
      if (task.deadline) {
        const deadlineDate = new Date(task.deadline);
        
        // ËÆ°ÁÆóÊó∂Èó¥Â∑ÆÔºàÊØ´ÁßíÔºâ
        const timeDiff = deadlineDate - today;
        
        // Â¶ÇÊûúÊó∂Èó¥Â∑≤ËøáÊúü
        if (timeDiff <= 0) {
          return `Â∑≤ËøáÊúü`;
        }
        
        // ËΩ¨Êç¢‰∏∫Â∞èÊó∂Êï∞
        const hoursLeft = Math.ceil(timeDiff / (1000 * 60 * 60));
        
        if (hoursLeft < 24) {
          // 24Â∞èÊó∂ÂÜÖÊòæÁ§∫Â∞èÊó∂Êï∞
          return `${hoursLeft}Â∞èÊó∂`;
        } else if (hoursLeft < 24 * 7) {
          // 7Â§©ÂÜÖÊòæÁ§∫Â§©Êï∞
          const daysLeft = Math.ceil(hoursLeft / 24);
          return `${daysLeft}Â§©`;
        } else {
          // Ë∂ÖËøá7Â§©ÊòæÁ§∫ÂÖ∑‰ΩìÊó•Êúü
          const year = deadlineDate.getFullYear();
          const month = (deadlineDate.getMonth() + 1).toString().padStart(2, '0');
          const day = deadlineDate.getDate().toString().padStart(2, '0');
          return `${year}-${month}-${day}`;
        }
      }
      
      return task.subjectName;
    },
    
    // Ê†ºÂºèÂåñÊà™Ê≠¢Êó∂Èó¥
    formatDeadline(deadline) {
      if (!deadline) return 'Êó†Êà™Ê≠¢Êó∂Èó¥';
      
      const now = new Date();
      const deadDate = new Date(deadline);
      const diffDays = Math.floor((deadDate - now) / (1000 * 60 * 60 * 24));
      
      if (diffDays === 0) {
        return '‰ªäÂ§©Êà™Ê≠¢';
      } else if (diffDays === 1) {
        return 'ÊòéÂ§©Êà™Ê≠¢';
      } else if (diffDays === 2) {
        return 'ÂêéÂ§©Êà™Ê≠¢';
      } else if (diffDays > 0) {
        return `${diffDays}Â§©ÂêéÊà™Ê≠¢`;
      } else {
        return 'Â∑≤ÈÄæÊúü';
      }
    },
    
    // Ê†ºÂºèÂåñ‰ºòÂÖàÁ∫ß
    formatPriority(priority) {
      switch (priority?.toLowerCase()) {
        case 'high':
        case 'È´ò':
          return 'high';
        case 'medium':
        case '‰∏≠':
          return 'medium';
        case 'low':
        case '‰Ωé':
          return 'low';
        default:
          return 'low';
      }
    },
    
    // Ê†ºÂºèÂåñ‰ºòÂÖàÁ∫ßÊñáÊú¨
    formatPriorityText(priority) {
      switch (priority?.toLowerCase()) {
        case 'high':
        case 'È´ò':
          return 'È´ò';
        case 'medium':
        case '‰∏≠':
          return '‰∏≠';
        case 'low':
        case '‰Ωé':
          return '‰Ωé';
        default:
          return 'ÊôÆÈÄö';
      }
    },
    
    // ‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ‰Ωú‰∏∫ÂêéÂ§á
    useMockData() {
      this.todayTaskList = [
        {
          id: 1,
          title: 'ÂÆåÊàêÊï∞Â≠¶‰Ωú‰∏öÁ¨¨5Á´†',
          time: 'Êï∞Â≠¶ 11:00'
        },
        {
          id: 2,
          title: 'Â§ç‰π†Ëã±ËØ≠ÂçïËØçÊú¨',
          time: 'Ëã±ËØ≠ 14:00'
        },
        {
          id: 3,
          title: 'ËÉåËØµ',
          time: 'ËØ≠Êñá 16:00'
        }
      ];
      
      this.upcomingTaskList = [
        {
          id: 4,
          title: 'Êèê‰∫§ËØæÁ®ãËÆ∫Êñá',
          deadline: 'ÊòéÂ§©Êà™Ê≠¢',
          priority: 'high',
          priorityText: 'È´ò'
        },
        {
          id: 5,
          title: 'ÂéÜÂè≤',
          deadline: 'ÂêéÂ§©Êà™Ê≠¢',
          priority: 'medium',
          priorityText: '‰∏≠'
        },
        {
          id: 6,
          title: 'ÁºñÁ®ãÈ°πÁõÆ‰∏ÄÈò∂Êèê‰∫§',
          deadline: 'ÂêéÂ§©Êà™Ê≠¢',
          priority: 'low',
          priorityText: '‰Ωé'
        }
      ];
    },
    
    // ‰ªéÊúçÂä°Âô®Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
    async fetchUserInfo() {
      try {
        const res = await userAPI.getUserInfo();
        this.userInfo = res.data;
      } catch (error) {
        console.error('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error);
      }
    },
    
    // ÈÄÄÂá∫ÁôªÂΩï
    async logout() {
      uni.showModal({
        title: 'ÈÄÄÂá∫ÁôªÂΩï',
        content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
        confirmText: 'Á°ÆÂÆö',
        cancelText: 'ÂèñÊ∂à',
        success: async (res) => {
          if (res.confirm) {
            try {
              // Ë∞ÉÁî®ÈÄÄÂá∫ÁôªÂΩïAPI
              await userAPI.logout();
              // Ê∏ÖÈô§Êú¨Âú∞Êï∞ÊçÆ
              clearUserData();
              // Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
              uni.reLaunch({
                    url: '/pages/login/login'
              });
            } catch (error) {
              console.error('ÈÄÄÂá∫ÁôªÂΩïÂ§±Ë¥•:', error);
              // Âç≥‰ΩøAPIË∞ÉÁî®Â§±Ë¥•Ôºå‰πüË¶ÅÊ∏ÖÈô§Êú¨Âú∞Êï∞ÊçÆÂπ∂Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ
              clearUserData();
              uni.reLaunch({
                    url: '/pages/login/login'
              });
            }
          }
        }
      });
    },
    
    viewAllTasks(type) {
      // ËøôÈáåÂèØ‰ª•ÂÆûÁé∞Ë∑≥ËΩ¨Âà∞‰ªªÂä°ÂàóË°®È°µÈù¢
      uni.showToast({
        title: `Êü•ÁúãÂÖ®ÈÉ®${type === 'today' ? '‰ªäÊó•' : 'Âç≥Â∞ÜÊà™Ê≠¢'}‰ªªÂä°`,
        icon: 'none'
      })
      // Á§∫‰æãÔºöuni.navigateTo({ url: `/pages/task/list?type=${type}` });
    },
    
    handleTaskComplete(task) {
      // Â§ÑÁêÜ‰ªªÂä°ÂÆåÊàêÈÄªËæë
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®APIÊõ¥Êñ∞‰ªªÂä°Áä∂ÊÄÅ
      uni.showToast({
        title: '‰ªªÂä°Â∑≤ÂÆåÊàê',
        icon: 'success'
      })
      
      // ‰ªéÂàóË°®‰∏≠ÁßªÈô§Â∑≤ÂÆåÊàêÁöÑ‰ªªÂä°
      const todayIndex = this.todayTaskList.findIndex(t => t.id === task.id);
      if (todayIndex !== -1) {
        this.todayTaskList.splice(todayIndex, 1);
      }
      
      const upcomingIndex = this.upcomingTaskList.findIndex(t => t.id === task.id);
      if (upcomingIndex !== -1) {
        this.upcomingTaskList.splice(upcomingIndex, 1);
      }
      
      // Êõ¥Êñ∞ÁªüËÆ°
      this.statistics.taskCount = this.todayTaskList.length;
    }
  }
}
</script>

<style lang="scss" scoped>
.main-container {
  min-height: 100vh;
  background-color: #F8FAFF;
  padding: 30rpx 20rpx 120rpx;
}

.header {
    padding: 20rpx;
    margin-bottom: 30rpx;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    .page-title {
      font-size: 36rpx;
      font-weight: 600;
      color: #333333;
    }
    
    .user-info {
      display: flex;
      align-items: center;
      padding: 10rpx;
      border-radius: 12rpx;
    }
    
    .avatar {
      width: 60rpx;
