      border-radius: 50%;
      background-color: #5374f7;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 16rpx;
    }
    
    .avatar-text {
      color: #ffffff;
      font-size: 28rpx;
      font-weight: 600;
    }
    
    .user-details {
      display: flex;
      flex-direction: column;
    }
    
    .user-name {
      font-size: 28rpx;
      font-weight: 500;
      color: #333333;
    }
    
    .logout-text {
      font-size: 22rpx;
      color: #999999;
      margin-top: 2rpx;
    }
  }

.stats-container {
  display: flex;
  justify-content: space-between;
  gap: 20rpx;
  margin-bottom: 30rpx;
  
  .stat-card {
    flex: 1;
    background-color: #ffffff;
    border-radius: 20rpx;
    padding: 28rpx 0;
    text-align: center;
    box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.05);
    
    .stat-number {
      display: block;
      font-size: 48rpx;
      font-weight: 600;
      color: #5374f7;
      margin-bottom: 8rpx;
    }
    
    .stat-label {
      font-size: 24rpx;
      color: #666666;
    }
  }
}

.today-tasks,
.upcoming-tasks {
  background-color: #ffffff;
  border-radius: 20rpx;
  padding: 28rpx;
  margin-bottom: 30rpx;
  box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.05);
  
  .section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 28rpx;
    
    .section-title {
      font-size: 32rpx;
      font-weight: 600;
      color: #333333;
    }
    
    .section-more {
      font-size: 26rpx;
      color: #5374f7;
    }
  }
  
  .task-list {
        
        .empty-state {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          padding: 60rpx 0;
          text-align: center;
          
          .empty-icon {
            font-size: 80rpx;
            margin-bottom: 20rpx;
          }
          
          .empty-text {
            font-size: 28rpx;
            color: #333333;
            margin-bottom: 10rpx;
          }
          
          .empty-tip {
            font-size: 24rpx;
            color: #999999;
          }
        }
        
        .task-item {
      display: flex;
      align-items: flex-start;
      padding: 16rpx 0;
      border-bottom: 1rpx solid #f0f0f0;
      
      &:last-child {
        border-bottom: none;
      }
      
      .task-checkbox {
        transform: scale(0.8);
        margin-right: 16rpx;
        margin-top: 8rpx;
      }
      
      .task-content {
        flex: 1;
        
        .task-title {
          display: block;
          font-size: 30rpx;
          color: #333333;
          margin-bottom: 8rpx;
        }
        
        .task-time {
          font-size: 24rpx;
          color: #999999;
        }
        
        .task-deadline {
          display: flex;
          align-items: center;
          
          .deadline-label {
            font-size: 24rpx;
            color: #999999;
            margin-right: 12rpx;
          }
          
          .priority-tag {
            padding: 4rpx 16rpx;
            border-radius: 12rpx;
            font-size: 20rpx;
            
            &.high {
              background-color: #ffe6e6;
              color: #ff4d4f;
            }
            
            &.medium {
              background-color: #fff7e6;
              color: #faad14;
            }
            
            &.low {
              background-color: #e6f7ff;
              color: #1890ff;
            }
          }
        }
      }
    }
  }
}
</style>
